import React, {Component} from 'react';
import { isMobile } from 'react-device-detect';
import { Link as ReactLink } from 'react-router-dom';
class Link extends Component{
  state = {
      left: '-200%'
  }
  constructor(props)
  {
    super(props);
    this.state.style=this.props.style;
    this.state.to =this.props.to;
    this.onClick = this.props.onClick;
    this.state.color = this.props.color;
  }
  render()
  {
    return(
      <div className='link' style={{
        position: 'relative',
        // marginLeft: 50,
        width: 'fit-content',
        display: 'block',
        overflow: 'hidden',
        // background: '#aaa',
        ...this.state.style,
    }} 
    onMouseEnter={() => { this.setState({
        left: '0%'
    })}}
    onMouseLeave={() => { this.setState({
        left: '-200%'
    })}}
    onClick={this.onClick}>
      <ReactLink to={this.props.to} style={{
            color: this.state.color||'#000',
            textDecoration: 'none',
        }}>
            {this.props.children}
      </ReactLink>
        <div class='link-underline' style={{
          left: isMobile?'0%':this.state.left,
          position: 'relative',
          background: this.state.color||'#000',
          width: '100%',
          height: '2px',
          transition: 'all 0.3s ease-out 0.01s, background-color 1s ease-out'
        }}></div>
      </div>
    );
  }
}
  
export default Link;