import React, {Component} from 'react';
import {isMobile} from "react-device-detect";
import manifesto from './pics/manifesto.jpg';
import ulgulan from './pics/ulgulan.jpg';
import screenings from './pics/screenings2.jpg';
import programs from './pics/programs.jpg';
import posters from './pics/posters.jpg';
import murals from './pics/murals.jpg';
import zine from './pics/zine.jpg';
import Link from '../components/link';
import Enlarge from '../components/enlarge';

class Choice extends Component{
    state = {
    }

    constructor(props)
    {
        super(props);
        this.state.src = this.props.src;
        this.state.title = this.props.title;
        this.state.to = this.props.to;
    }

    render()
    {
        return (
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                margin: '1em 0.3em',
                fontSize: '1.2em',
                alignItems: 'center',
            }}>
                <Enlarge size={3}>
                    <img src={this.state.src} style={{
                    // background: '#c52137',
                    // maxWidth: '30vw',
                    maxHeight: '55vh',
                    maxWidth: '90vw'
                }}></img>
                </Enlarge>
                <Enlarge>                    
                    <Link color={"#c52137"} to={this.props.to}>
                    {this.state.title}
                    </Link>
                </Enlarge>
            </div>
        )
    }
}

class Menu extends Component{
    render()
    {
        return(
            <div style={{
                // width: '100vw',
                paddingTop: isMobile?'10vh':'0em',
                paddingBottom:'10em',
                minHeight: '100vh',
                position: 'relative',
                display: 'flex',
                flexDirection: 'row',
                flexWrap: 'wrap',
                backgroundColor: 'var(--bg2)',
                alignItems: 'center',
                justifyContent: 'center',
                color: 'var(--fg2)',
            }} id="menu">
                <Choice title={"Manifesto"} src={manifesto} to={"/manifesto"}></Choice>
                <Choice title={"Ulgulan"} src={ulgulan}></Choice>
                <Choice title={"Screenings"} src ={screenings}></Choice>
                <Choice title={"Programmes"} src={programs}></Choice>
                <Choice title={"Posters"} src={posters}></Choice>
                <Choice title={"Murals"} src={murals}></Choice>
                <Choice title={"Weekly Zine"} src={zine}></Choice>
            </div>
        );
    }
}

export default Menu;

