import React, {Component} from 'react';

class LoadingScreen extends Component{
    render()
    {
        return (<div style={{
                width: "100vw",
                height: "100vh",
                background: "#c52137",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                color: "var(--fg)",
                fontSize: "2em",
                fontFamily: "Playfair Display",
                fontStyle: 'italic',
            }}>
                Loading...
            </div>);
    }
}

class Preloader extends Component{
    state = {
        loaded: false
    }

    constructor(props)
    {
        super(props);
        this.preloadSrcList = this.props.preloadSrcList;
    }

    async componentDidMount()
    {
        // console.log(this.preloadSrcList);
        const imagesPromiseList = []
        for (const i of this.preloadSrcList) {
          imagesPromiseList.push(this.preloadImage(i))
        }
    
        await Promise.all(imagesPromiseList)

        this.setState({
            loaded: true
        });
    }

    preloadImage (src) {
        return new Promise((resolve, reject) => {
          const img = new Image()
          img.onload = function() {
            resolve(img)
          }
          img.onerror = img.onabort = function() {
            reject(src)
          }
          img.src = src
        })
    }

    render()
    {
      return(
        <div
        id="preloader"
            style={{    

        }}>
            {this.state.loaded?this.props.children:<LoadingScreen/>}
        </div>
      );
    }
}
  
export default Preloader;