import { createReducer } from '@reduxjs/toolkit'
import toggleLang from './langAction';

const initialState = {
    lang: 'ENG'
}

const langReducer = createReducer(initialState, (builder) => {
    builder
      .addCase(toggleLang, (state, action) => {
        state.lang = (state.lang==='ENG')?'BEN':'ENG'
      });
  })

export default langReducer;