import React, {Component} from 'react';
import {isMobile} from "react-device-detect";
import PicCursor from './picCursor';
import logo from '../assets/logo.png';
import Sketch from './sketch';

class Title extends Component{

    mouseEnter()
    {
        const rem = new CustomEvent('remove');
        window.dispatchEvent(rem);
    }

    mouseLeave()
    {
        const add = new CustomEvent('reset');
        window.dispatchEvent(add);
    }

    render()
    {
      return(
        <div 
        style={{
            width: '100vw',
            height: '100vh',
            position: 'relative',
            display: 'flex',
            flexDirection: isMobile?'column':'row',
            backgroundColor: 'var(--bg)',
            alignItems: 'center',
            justifyContent: 'center',
            fontSize: isMobile?'35px':'50px',
            color: 'var(--fg)',
            // overflow: 'hidden',
            cursor: 'none'
        }}
        onMouseEnter={this.mouseEnter}
        onMouseLeave={this.mouseLeave}
        >
            <Sketch></Sketch>
            <PicCursor></PicCursor>
            
            <div style={{
                height: isMobile?'auto':'100vh',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                // background: 'rgba(0,0,0,0.5)'
            }}>
                <div style={{
                        display: 'block',
                        position: isMobile?'absolute':'relative',
                        top: isMobile?'33%':'-0.8em',
                        left:isMobile?'33%':'5%',
                        fontSize: '4.5em',
                        fontFamily: 'Chomsky',
                        zIndex: 25,
                        pointerEvents: 'none',
                        margin: 0,
                        // background: 'rgba(0,0,0,0.5)',
                        transform: isMobile?'translate( -50%, -50%)':'',
                
                }}>Art</div>
                <div style={{
                        display: 'block',
                        fontFamily: 'Playfair Display',
                        fontStyle: 'italic',
                        // transform: 'translate( 0%, -25%)',
                        fontSize: '1.2em',
                        zIndex: 25,
                        margin: 0,
                        pointerEvents: 'none',
                        // background: 'rgba(0,0,0,0.5)'
                }}>against</div>
                <div style={{
                        display: 'block',
                        position: isMobile?'absolute':'relative',
                        top: isMobile?'67%':'1.6em',
                        left:isMobile?'60%':'0%',
                        fontSize: '2em',
                        lineHeight: '0.8em',
                        fontFamily: 'Roboto Condensed',
                        letterSpacing: '-0.08em',
                        zIndex: 25,
                        pointerEvents: 'none',
                        padding:0,
                        // background: 'rgba(0,0,0,0.5)',
                        transform: isMobile?'translate( -50%, -50%)':'',
                }}>OPPRE-<br/>SSION</div>
            </div>

            <div style={{
                display: 'block',
                width: isMobile?'30vw':'30vh',
                height: isMobile?'30vw':'30vh',
                position: 'absolute',
                top: isMobile?'25vw':'70%',
                left: isMobile?'80vw':'20%',
                transform: 'translate( -50%, -50%)',
                zIndex: 25,
                pointerEvents: 'none',
                background: 'url('+logo+') no-repeat center',
                backgroundSize: 'contain'
            }}>

            </div>
        </div>
      );
    }
}

export default Title;