import React, {useEffect, useRef, useState} from 'react';
import { isMobile } from 'react-device-detect';
import { useSelector} from 'react-redux';
import NavBar from '../components/navbar';

const About = () => {

    const [fact, setFact] = useState("");
    const myRef = useRef();

    const lang = useSelector( state => state.lang);
    const englishText = " is a cultural forum\
    that welcomes the collective voices\
    and artforms which speak up against\
    the existing oppressive social order.\
    \n\
    We are trying to bring together\
    an organised expression of those cultures\
    which have always stood by the oppressed.";

    const bengaliText = " একটি সাংস্কৃতিক ফোরাম\
    যা বিদ্যমান নিপীড়নমূলক সামাজিক\
    ব্যবস্থার বিরুদ্ধে কথা বলার যৌথ\
    কণ্ঠস্বর এবং শিল্পকলাকে স্বাগত জানায়।\
    \n\
    আমরা সেই সব সংস্কৃতির\
    একটি সংগঠিত অভিব্যক্তিকে\
    একত্রিত করার চেষ্টা করছি যারা সর্বদা নিপীড়িতদের পাশে দাঁড়িয়েছে।";


    // }

    useEffect(() => {
        window.addEventListener("scroll", () => {
            if(myRef.current)
            {
                let off = document.documentElement.scrollTop - myRef.current.offsetTop - window.innerHeight*0.25;
                off = off/(window.innerHeight/2);
                if(off<0)off=0;
                if(off>1)off=1;
                setFact(off);
            }
        });
    },[]);

    let text = (lang==="ENG")?englishText:bengaliText;
    let lim  = Math.floor(fact*text.length);
    let currentText = text.substring(0,lim);
    let displayText = currentText.split("\n").map( (txt) => {
        return (<p style={{
            padding: isMobile?'0em 1em':'0em 2em'
            }}>{txt}</p>);
    });

        // console.log(displayText);
        return (
            <div style={{
                minHeight: isMobile?'50vh':'100vh',
                position: 'relative',
                margin: 0,
                // display: 'flex',
                // flexWrap: 'wrap',
                backgroundColor: 'var(--bg2)',
                color: 'var(--fg2)',
                fontSize: isMobile?'1.3em':'2em',
            }}>
                <div style={{
                fontSize: isMobile?'0.76em':'0.5em',
                padding: isMobile?'5vh 0.5em':'5vw 2em',
                paddingBottom: 0,
                }}>
                <NavBar></NavBar>
                </div>
                <div style={{
                paddingBottom: '2em',
                width: isMobile?'100%':'50%',
                fontFamily: (lang==='ENG')?'Playfair Display':'Kalpurush',
                fontSize:  (lang==='ENG')?"1em":"1.1em",
                lineHeight:  (lang==='ENG')?"1.3em":"1.2em",
                }}>
                    <p 
                    ref={myRef}
                    style={{
                        padding: isMobile?'0em 1em':'0em 2em',
                        margin: 0,
                        paddingRight: '1em',
                        float: 'left'
                        }}>
                        <span style={{
                        color: 'var(--bg)',
                        }}>
                            {(lang==='ENG')?'Art Against Oppression':'আর্ট এগেনস্ট অপ্রেশন'}
                        </span>
                    </p>
                    {displayText}
                </div>
            </div>
        )
    // }
}

export default About;