import React, {Component} from 'react';
import MediaQuery from 'react-responsive';
import {isMobile} from "react-device-detect";

class Cursors extends Component{

  state = {
    x:-100,
    y:-100,
    col: 347,//167,//265,
    size: 50,
    show: 1,
    mainSize: 15,
  }
  componentDidMount()
  {
    this.setState({
      wid: window.innerWidth
    });
    
    if(isMobile) return;
    window.addEventListener('mousemove', (e)=>{
        this.setState({
        x:e.clientX,
        y:e.clientY,
        });
    });

    window.addEventListener('enlarge', (e) => {
      this.setState({
        size: 50*e.detail
      });
    });

    window.addEventListener('enlargeMain', (e) => {
      this.setState({
        mainSize: 30,
        size: 0,
        show: 0
      });
    });

    window.addEventListener('reset', (e) => {
      this.setState({
        size: 50,
        mainSize: 15,
        show: 1
      });
    });
    window.addEventListener('remove', (e) => {
      this.setState({
        size: 0,
        show: 0,
        mainSize: 0,
      });
    });
  }
  render()
  {
    return(
      <>
      <div style={{
          backgroundColor: '#fff',
          width:this.state.size,
          height:this.state.size,
          borderRadius: this.state.size/2,
          position:'fixed',
          top:this.state.y-this.state.size/2,
          left:this.state.x-this.state.size/2,
          transition: "all 0.3s ease-out 0.01s, background-color 1s ease-out",
          pointerEvents: 'none',
          mixBlendMode: 'exclusion',
          zIndex: 98,
      }}></div>
      <div style={{
          backgroundColor: '#000',
          width:this.state.size,
          height:this.state.size,
          borderRadius: this.state.size/2,
          position:'fixed',
          top:this.state.y-this.state.size/2,
          left:this.state.x-this.state.size/2,
          transition: "all 0.3s ease-out 0.01s, background-color 1s ease-out",
          pointerEvents: 'none',
          mixBlendMode: 'saturation',
          zIndex: 98,
      }}></div>
      <div style={{
          backgroundColor: '#c52137',
          width:this.state.size,
          height:this.state.size,
          borderRadius: this.state.size/2,
          position:'fixed',
          top:this.state.y-this.state.size/2,
          left:this.state.x-this.state.size/2,
          transition: "all 0.3s ease-out 0.01s, background-color 1s ease-out",
          pointerEvents: 'none',
          mixBlendMode: 'screen',
          zIndex: 98,
      }}></div>
      <div
          style={{
            backgroundColor: '#000',
            width:this.state.mainSize,//this.state.show?15:0,
            height:this.state.mainSize,//this.state.show?15:0,
            borderRadius: this.state.mainSize/2,
            position:'fixed',
            top:this.state.y-this.state.mainSize/2,
            left:this.state.x-this.state.mainSize/2,
            transition: "all 0.1s ease-out",
            pointerEvents: 'none',
            // mixBlendMode: 'exclusion',
            zIndex: 98,
          }}>
        </div>
      </>
    );
  }
}

class Layout extends Component{
    render()
    {
      return(
        <div
              style={{
            cursor: 'none',
            // height: "100%",
        }}>

            {this.props.children}
            <MediaQuery minDeviceWidth={1224}>
            <Cursors></Cursors>
            </MediaQuery>
        </div>
      );
    }
}
  
export default Layout;