import React, {Component} from 'react';
// import MediaQuery from 'react-responsive';
// import {isMobile} from "react-device-detect";

  

class Enlarge extends Component{
  state = {
    size: 2
  }
  constructor(props)
  {
    super(props);
    this.state.style = this.props.style;
    this.state.size = this.props.size || 2;
    this.mouseEnter = this.mouseEnter.bind(this);
  }

  mouseEnter() {
    const enlarge = new CustomEvent('enlarge', { detail: this.state.size });
    window.dispatchEvent(enlarge);
  }

  mouseLeave() {
    const reset = new CustomEvent('reset', { detail: 1.5 });
    window.dispatchEvent(reset);
  }

  render()
  {
    return(
      <span 
      style={{...this.state.style}}
      onMouseEnter={this.mouseEnter}
      onMouseLeave={this.mouseLeave}
      >
          {this.props.children}
      </span>
    );
  }
}
  
export default Enlarge;