import React, {Component} from 'react';

class ParallaxImage extends Component{

    state = {
        fact: 0,
        excessHeight: 0
    }

    constructor(props)
    {
        super(props);
        this.myRef = React.createRef();
        this.style = this.props.style || {};
        this.state.width = this.props.width;
        this.state.height = this.props.height;
        this.state.imgstyle = {};
        // console.log("aa",this.state.width, this.props.pmeasure?"vw":"");
    }

    async componentDidMount()
    {
        try {
            const img = await this.preloadImage(this.props.src);
            // console.log(img.width,img.height);

            const imgRatio = img.width/img.height;
            let boxRatio = this.state.width/this.state.height;
            if(this.props.pmeasure)
            {
                boxRatio =  boxRatio*window.innerWidth/window.innerHeight;
            }

            let boxHeight = this.state.height
                            *(this.props.pmeasure?window.innerHeight/100:1);
            let excessHeight = 0;
            if(boxRatio > imgRatio)
            {
                //box is wider than image
                //image width = box width
                this.setState({
                        imgstyle : {
                        width: this.state.width + (this.props.pmeasure?"vw":"px"),
                        height: "auto"
                    }
                });
                let imgHeight = this.state.width
                                *(this.props.pmeasure?window.innerWidth/100:1)
                                /(imgRatio);

                excessHeight = imgHeight-boxHeight;
                // console.log(this.state.imgstyle);
            }
            else
            {
                //box is taller than image
                //image height = box height
                const enlr = this.props.preventEnlarge?1:1.5;
                this.setState({
                    imgstyle : {
                        height: this.state.height*enlr +(this.props.pmeasure?"vh":"px")
                    }
                });
                let imgHeight = this.state.height*enlr
                                *(this.props.pmeasure?window.innerHeight/100:1)
    
                excessHeight = imgHeight-boxHeight;
                // console.log(this.state.imgstyle);
            }
            this.setState({
                excessHeight: excessHeight
            });
        } catch(e) {
            // console.log(e);
        }
        window.addEventListener("scroll", () => {
            if(this.myRef.current)
            {
                let off = document.documentElement.scrollTop - this.myRef.current.offsetTop + window.innerHeight*0.75;
                off = off/(window.innerHeight*0.75);
                if(this.myRef.current.offsetTop < window.innerHeight)
                {
                    off = document.documentElement.scrollTop/this.myRef.current.offsetTop;
                    // console.log(off,document.documentElement.scrollTop,this.myRef.current.offsetTop);
                }
                if(off<0)off=0;
                if(off>1)off=1;
                // console.log(off);
                this.setState({
                    fact: off
                });
            }
        });
    }

    preloadImage (src) {
        return new Promise((resolve, reject) => {
          const img = new Image()
          img.onload = function() {
            resolve(img)
          }
          img.onerror = img.onabort = function() {
            reject(src)
          }
          img.src = src
        })
    }

    render()
    {
        // console.log(this.state.width +(this.props.pmeasure?"vw":"px"));
        // console.log(this.state.fact*this.state.excessHeight);
        
        // console.log(this.state.imgstyle);
        return(
            <div 
            ref={this.myRef}
            style={{
                width: this.state.width +(this.props.pmeasure?"vw":"px"),
                height: this.state.height +(this.props.pmeasure?"vh":"px"),
                // background: "#09f",
                overflow: "hidden",
                position: "relative",
                display: "flex",
                justifyContent: "center",
                alignItems: "flex-start",
                // background: "rgb(255,"+this.state.fact*255+",0)",
                ...this.style
            }}>
                <img
                style={{
                    position: "relative",
                    top: -this.state.fact*this.state.excessHeight,
                    ...this.state.imgstyle}}
                src={this.props.src}/>
            </div>
        );
    }
}

export default ParallaxImage;