import React, {Component} from 'react';

class SpeedDiv extends Component{

    state = {
        fact: 0,
        shift:0
    }

    constructor(props)
    {
        super(props);
        this.myRef = React.createRef();
        this.style = this.props.style || {};
        this.initOff = 0;
        this.speed = this.props.speed -1;
    }

    async componentDidMount()
    {
        if(this.myRef.current)
        {
            this.initOff = this.myRef.current.offsetTop;
        }
        window.addEventListener("scroll", () => {
            if(this.myRef.current)
            {
                let thres = Math.max(this.initOff,window.innerHeight*0.75);
                let off = document.documentElement.scrollTop - thres + window.innerHeight*0.75;
                // off = off/(window.innerHeight*0.75);
                // if(this.myRef.current.offsetTop < window.innerHeight)
                // {
                //     off = document.documentElement.scrollTop/this.initOff;
                // }
                // if(off<0)off=0;
                // if(off>1)off=1;

                this.setState({
                    shift: off
                });
            }
        });
    }

    render()
    {
        return(
            <div 
            ref={this.myRef}
            style={{
                position: "relative",
                top:-this.state.shift*this.speed,
                ...this.style
            }}>
                {this.props.children}
            </div>
        );
    }
}

export default SpeedDiv;