import React, {Component} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { isMobile } from 'react-device-detect';
import logo from '../assets/logo.png';
import { Link } from 'react-router-dom';
import Enlarge from './enlarge';
import toggleLang from '../redux/lang/langAction';

const LanguageSwitch = () => {

    const lang = useSelector( state => state.lang);
    const dispatch = useDispatch();

    const toggleLanguage = () => {
        dispatch(toggleLang());
    }

    return (
        <div style={{
            marginBottom: "1em",
            marginLeft: "1em",
            marginRight: isMobile?"":"1.7em",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            color: "var(--bg)",
            transform: isMobile?"scale(0.8)":""
        }}>
            <div style={{
                fontFamily: "Be Vietnam",
                fontSize: "0.8em",
                lineHeight: "1em"
            }}>
                ENGLISH
            </div>

            <Enlarge size={2}> 
            <div style={{
                margin: "0em 0.4em",
                border: "1px solid var(--bg)",
                borderRadius: "1em",
                height: "1em",
                width: "1.7em",
                position: "relative",
                cursor: "pointer",
                outline: "none",
                WebkitTapHighlightColor: "transparent",
            }}
            onClick={toggleLanguage}>
                <div style={{
                    background: "var(--bg)",
                    borderRadius: "1em",
                    position: "absolute",
                    height: "1em",
                    width: "1em",
                    right: (lang==='BEN')? 0 : "auto"
                }}>
                </div>
            </div>
            </Enlarge>

            <div style={{
            fontFamily: "Kalpurush",
            fontSize: "1em",
            lineHeight: "1em"
            }}>
                বাংলা
            </div>
        </div>
    );
}

class NavBar extends Component{
    render()
    {
        return (
            <div style={{
                width: "100%",
                padding: "0.7em 0em",
                position: "relative",
                color: "var(--bg)",
                fontFamily: "Playfair Display",
                fontSize: isMobile?"1.3em":"1.3em",
                display: "flex",
                flexDirection: "row-reverse",
                flexWrap: "wrap",
                // background: "rgba(0,0,0,0.2)"
                // isMobile?"column":
            }}>

                <LanguageSwitch/>

                
                <div style={{flex:1}}>
                </div>

                <div style={{
                    display: "flex",
                    flexDirection: "row",
                    alignItems: "center",
                    lineHeight: "1.2em",
                    // background: "rgba(0,0,0,0.2)",
                    marginRight: "auto",
                    transform: "scale(0.9)"
                }}>

                    <Enlarge size={1.2}>   
                    <Link to={"/"}>
                    <div style={{
                        display: 'block',
                        width: '4.5em',
                        height: '4.5em',
                        // margin: '0em 0.4em',
                        zIndex: 25,
                        background: 'url('+logo+') no-repeat center',
                        backgroundSize: 'contain',
                    }}>
                    </div>
                    </Link>
                    </Enlarge>
                    <Enlarge size={1.2}>  
                    <Link to={"/"} style={{
                        color: 'inherit',
                        textDecoration: 'none'
                    }}>
                    <div>
                        Art<br/>Against<br/>Oppression
                    </div>
                    </Link>
                    </Enlarge>  
                </div>
                


            </div>
        )
    };
}

  
export default NavBar;